/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --darkBlue: hsl(240, 38%, 20%);
  --grayshBlue: hsl(240, 18%, 77%);
  --white: hsl(0, 100%, 100%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.wrapper {
  background-image: url(../images/pattern-curve.svg);
  background-repeat: no-repeat;
  background-size: 75% 15%;
  background-position: left bottom;
}

.container {
  max-width: 32.7rem;
  margin: 0 auto;
  height: 100vh;
}
@media (min-width: 37.5em) {
  .container {
    max-width: 54rem;
  }
}
@media (min-width: 48em) {
  .container {
    max-width: 68rem;
  }
}
@media (min-width: 64em) {
  .container {
    max-width: 90rem;
  }
}
@media (min-width: 75em) {
  .container {
    max-width: 110rem;
  }
}

html {
  font-size: 62.5%;
}

body {
  font-family: var(--font-inter);
  font-weight: 300;
  color: var(--darkBlue);
}

.heading-secondary {
  font-size: 1.5rem;
}
@media (min-width: 48em) {
  .heading-secondary {
    font-size: 2rem;
  }
}

.profile {
  display: grid;
}
@media (min-width: 48em) {
  .profile {
    grid-template-columns: 1.17fr 1fr;
    align-items: center;
  }
}
.profile__image-content {
  position: relative;
  padding: 3.6rem;
  background-image: url(../images/pattern-bg.svg);
  background-size: contain;
  background-repeat: no-repeat;
}
@media (min-width: 37.5em) {
  .profile__image-content {
    padding: 4.8rem;
  }
}
@media (min-width: 48em) {
  .profile__image-content {
    grid-column: 2/-1;
    padding: 2.4rem;
  }
}
@media (min-width: 64em) {
  .profile__image-content {
    padding: 6rem;
  }
}
.profile__image {
  border-radius: 5px;
  box-shadow: 0 4rem 5rem -3rem rgba(32, 32, 70, 0.5);
}
.profile__description-content {
  text-align: center;
}
@media (min-width: 48em) {
  .profile__description-content {
    grid-row: 1/2;
    text-align: left;
    width: 110%;
    z-index: 2;
  }
}
@media (min-width: 64em) {
  .profile__description-content {
    width: 122%;
  }
}
@media (min-width: 75em) {
  .profile__description-content {
    width: 125%;
  }
}
.profile__text {
  font-size: 1.8rem;
  color: var(--darkBlue);
  font-weight: 300;
  margin-bottom: 2rem;
}
@media (min-width: 48em) {
  .profile__text {
    font-size: 2.4rem;
  }
}
@media (min-width: 75em) {
  .profile__text {
    font-size: 3.2rem;
  }
}
.profile__status {
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
}
@media (min-width: 48em) {
  .profile__status {
    align-items: center;
    flex-direction: row;
    column-gap: 1rem;
    font-size: 2rem;
  }
}
.profile__job {
  color: var(--grayshBlue);
  font-weight: 500;
}

.slider {
  position: relative;
  overflow: hidden;
}
.slider__slide {
  transition: transform 0.4s;
  position: absolute;
  top: 50%;
}
.slider__button-content {
  display: flex;
  position: absolute;
  width: 8rem;
  height: 4rem;
  bottom: 17px;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 37.5em) {
  .slider__button-content {
    bottom: 30px;
  }
}
@media (min-width: 48em) {
  .slider__button-content {
    bottom: 6px;
    left: 95px;
  }
}
@media (min-width: 64em) {
  .slider__button-content {
    width: 11.2rem;
    height: 5.6rem;
    bottom: 33px;
    left: 140px;
  }
}
.slider__button {
  background-color: var(--white);
  border: none;
  width: 50%;
  padding: 1.4rem 1.7rem;
  cursor: pointer;
}
@media (min-width: 64em) {
  .slider__button {
    padding: 2rem 2.4rem;
  }
}
.slider__button img {
  width: 100%;
  height: 100%;
}
.slider__button--prev {
  border-top-left-radius: 2.8rem;
  border-bottom-left-radius: 2.8rem;
}
.slider__button--next {
  border-top-right-radius: 2.8rem;
  border-bottom-right-radius: 2.8rem;
}